import React, { useState } from 'react'
import { useTransition, animated } from 'react-spring'
import closeSound from './sounds/close.mp3'
// import pageFlip from './sounds/page.mp3'
import project_data from './project_data'

const closeCabinet = new Audio(closeSound);
// const page = new Audio(pageFlip);

const CabinetTopMidModal = ({topOpen, setTopOpen}) => {

  const [pageCount, setPageCount] = useState(0);
  const [leftArrowIcon, setLeftArrow] = useState("<");
  const [rightArrowIcon, setRightArrow] = useState(">");

  // Function to close the modal
  const CloseModal = () => {
    setTopOpen(!topOpen);
    closeCabinet.volume = 0.5;
    closeCabinet.play();
    setPageCount(0);
    setRightArrow(">");
    setLeftArrow("<");
  };

  const right = () => {
    if(pageCount < project_data.length-1){
      setPageCount(pageCount + 1)
      setRightArrow(">");
      setLeftArrow("<");
    }
    else{
      setRightArrow("");
      setLeftArrow("<");
    }
    // page.volume = 0.5;
    // page.play();
  }

  const left = () => {
    if(pageCount !== 0){
      setPageCount(pageCount - 1);
      setLeftArrow("<");
      setRightArrow(">")
    }
    else{
      setLeftArrow("");
      setRightArrow(">");
    }
    // page.volume = 0.5;
    // page.play();
  }

  // Fade in and fade out
  const transitions = useTransition(topOpen, {
    from: { opacity: 0 },
    enter: { opacity: 1 },
    leave: { opacity: 0 },
  });
  
  // Mount/unmount
  return transitions(
    (styles, topOpen) =>
    topOpen && (
      <animated.div style={styles} className='modal-wrapper'>
        <div onClick={CloseModal} className='overlay' />
        <div className="projects">
          <div className="arrows">
            <p onClick={left} className="left">{leftArrowIcon}</p>
            <p onClick={right} className="right">{rightArrowIcon}</p>
          </div>
          <div className='modal-folder' >
            <div className='top'>
              <div className='header'>
                <h4 className="title">Projects</h4>
                <div onClick={CloseModal} className='close'>
                  x
                </div>
              </div>
            </div>
            <div className="content">
              {project_data[pageCount]}
            </div>
          </div>
        </div>
      </animated.div>
    )
  );
};

export default CabinetTopMidModal;