import React from 'react'
import { useTransition, animated } from 'react-spring'
import profile from './project_media/profile.jpg'
import closeSound from './sounds/close.mp3'

const closeCabinet = new Audio(closeSound);

const CabinetTopMidModal = ({topMidOpen, setTopMidOpen}) => {

  const CloseModal = () => {
    setTopMidOpen(!topMidOpen);
    closeCabinet.volume = 0.5;
    closeCabinet.play();
  };

  // Fade in and out
  const transitions = useTransition(topMidOpen, {
    from: { opacity: 0 },
    enter: { opacity: 1 },
    leave: { opacity: 0 },
  });
  
    // Mount/unmount
  return transitions(
    (styles, topMidOpen) =>
    topMidOpen && (
      <animated.div style={styles} className='modal-wrapper'>
        <div onClick={CloseModal} className='overlay' />
        <div className="projects">
          <div className='modal-folder' >
            <div className='top'>
              <div className='header'>
                <h4 className="title">About Me</h4>
                <div onClick={CloseModal} className='close'>
                  x
                </div>
              </div>
            </div>
            <div className="content">
              <h3 className="modalTitle">Hello!</h3>
              <p className="modalText">My name is Ryan Shihabi, I am 17 years old and currently live in Southern California. I have been teaching myself how to program since I was 10 years old and have always seen computer science as my way to express myself. As I head into college, I plan on studying Data Science because it combines my love for business and mathematics. I hope I can use the skills I have learned throughout my life to contribute to the workings of our world.</p>
              <div className="imageContainer">
                <img className="profile" src={profile}/>
              </div>
              
            </div>
          </div>
        </div>
      </animated.div>
    )
  );
};

export default CabinetTopMidModal;